<template>
  <div>
    <van-nav-bar
      title="系统设置"
      left-text=""
      left-arrow
      @click-left="$toPage(-1)"
    >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <van-list>
      <van-cell
        v-for="(item, index) in list"
        :key="index"
        :title="item.title"
        @click="clickItem(item)"
      />
    </van-list>
    <div class="button">
      <button
        @click="logout()"
        class="van-button van-button--danger van-button--large van-button--square"
      >
        <span data-v-fda1b190="" class="van-button__text">退出登陆</span>
      </button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      finished: false,
      list: [
        {
          title: "修改密码",
          router: "revise"
        },
        {
          title: "修改手机号码",
          router: "resetPhoneNumber"
        },
        {
          title: "清除缓存",
          fun: this.clearCache
        },
        {
          title: "意见反馈",
          router: "idea"
        },
        {
          title: "关于我们",
          router: "about"
        },
        {
          title: "版本更新",
          fun: this.appVersionUp
        }
      ]
    };
  },
  methods: {
    clickItem(it) {
      if (it.router) this.$toPage(it.router);
      else if (it.fun) it.fun();
    },
    logout() {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        mask: true,
        message: "退出登陆中..."
      });
      JNB.query({
        serverName: "userServer",
        cls: "LoginRegistrationModule",
        method: "logout",
        callback: data => {
          try {
            let result = JSON.parse(data);
            if (result.code == 200) {
              // 更新用户登录状态
              this.$removeUserInfo();
              this.$toast.success(result.message);
              this.$router.push({
                name: "index"
              });
            } else {
              this.$toast.fail(result.message);
            }
          } catch (e) {
            this.$toast.fail("网络错误");
          }
        }
      });
    },
    //清理缓存
    clearCache() {},
    //版本更新
    appVersionUp() {
      JNB._requestNative("versionUpdate");
    }
  }
};
</script>
<style scoped>
.button {
  height: 120px;
  width: 700px;
  margin: 0 auto;
}
.van-button.van-button--danger.van-button--large.van-button--square {
  width: 700px;
  border-radius: 15px;
  margin: 0 auto;
  position: fixed;
  bottom: 30px;
}
</style>
    